import React, { useState, useEffect } from 'react'
import Blog from './components/Blog'
import blogService from './services/blogss'
import BlogForm from './components/BlogForm'

const App = () => {

  const [ blogs, setBlogs ] = useState([])

  useEffect(() => {   
    blogService
    .getAll()    
    .then(initialBlogs => {     
        setBlogs(initialBlogs) 
           })
       }, []) 

  const [ newAuthor, setNewAuthor ] = useState('')
  const [ newTitle, setNewTitle ] = useState('')
  const [ newUrl, setNewUrl ] = useState('')
  const [ newLikes, setNewLikes ] = useState('')

  const addBlog = (event) => {
    event.preventDefault()
    const blogObject = {
      title: newTitle,
      author: newAuthor,
      url: newUrl,
      likes: newLikes
    
    }
 

    blogService
    .create(blogObject)
    .then(returnedBlog => {
      setBlogs(blogs.concat(returnedBlog))
      setNewTitle('')
      setNewAuthor('')
      setNewUrl('')
      setNewLikes('')
    })
   
  }

  const handleTitleChange = (event) => {
    setNewTitle(event.target.value)
  }

  const handleAuthorChange = (event) => {
    setNewAuthor(event.target.value)
  }

  const handleUrlChange = (event) => {
    setNewUrl(event.target.value)
  }

  const handleLikesChange = (event) => {
    setNewLikes(event.target.value)
  }

  return (
    <div>
      <h1>Blogposts</h1>
      <h2>add a new blog</h2>
      <BlogForm addPost={addBlog} newTitle={newTitle} handleTitleChange={handleTitleChange} newNumber={newAuthor} handleAuthorChange={handleAuthorChange}
      newUrl={newUrl} handleUrlChange={handleUrlChange} newLikes={newLikes} handleLikesChange={handleLikesChange} />
      <h2>Posts</h2>
      {blogs.map((blog, i) => {
        return ( 
          <p key={i}> 
      <Blog title={blog.title} author={blog.author} url={blog.url} likes={blog.likes} /> 
     {/* <button onClick={() => {personService.del(person.id, person.name, updateDB); deleteFromDBMessage(person.name)}}>delete</button> */}
      </p>
        )
      })}
    </div>
  )

}


export default App